



































import Vue from "vue";
import { DocListItem } from "./doc.interfaces";
import Day from "dayjs";
export default Vue.extend({
  props: {
    src: {
      type: Array as () => DocListItem[] | undefined,
      default: undefined
    }
  },
  methods: {
    onItemClick(slug: string) {
      this.$router.push({
        name: "post-id",
        params: {
          id: slug
        }
      });
    }
  }
});
